@charset "UTF-8";

.mango-theme {
    color: $mango-theme-default-text-color;
    background: #fffffd;

    a {
        color: $mango-theme-default-text-color;
    }

    .site-visual {
        background: linear-gradient(to bottom,#f09a1c 0%,#edd858 45%,rgba(255,255,255,0.701961) 45%,rgba(255,255,255,0.701961) 100%);
    }

    .menu-badge {
        border: 1px solid #b0bbe2;
        background: #8397df;
        color: #fbfdff;
     }
    
     // post container
     .post-container {
        .post-item {
            .tag-item {
                background: #f7e7c5;
                color: #96713a;     
                
                &:before {
                    border-right-color:#f7e7c5;
                }
            }
        }  
    }    

    // post content 
    .post-content {
        color: #3c3835;

        strong {
            color: #e4a62b;
        }

        p {
            color: #56514d;
             
        }

        .date {
            color: #716c67;
        }
    }
}

