@charset "UTF-8";

.post-content {
    text-align: center;
    font-size: 16px;

    strong {
        font-size: 17px;
    }

    img {
        max-width: 100%;
        max-height: 500px;
        border: 1px solid #eeeeee;
    }

    p {
        line-height: 1.5;
        margin: 2rem 0;
    }

    .other-post-title {
        font-size: 20px;
    }

    .related-post-container {
        list-style: none;
        text-align: left;
    }

    .title {
        font-weight: bold;
    }

    .map {
        max-width: 100%;
        margin: 0 auto;
    }
    
    .image-title {
        margin-top: 4rem;
        font-size: 1rem;
        font-weight: 550;

        &:before {
            margin-right: .3rem;
            font: normal normal normal 14px/1 FontAwesome;
        }

        &.location:before {
            content: '\f041';
        }

        &.menu:before {
            content: '\f0c9';
        }
    }

    .date {
        margin: 0 0 2rem 0;
        font-size: 13px;
    }
}