@charset "UTF-8";

.blue-theme {
    color: $blue-theme-default-text-color;
    background: #fdfeff;

    a {
        color: $blue-theme-default-text-color;
    }

    .site-visual {
        background: linear-gradient(to bottom,#86A8E7 0%,#a5c9e1 45%, #ffffffb3 45%, #ffffffb3 100%);
    }

    .menu-badge {
        background: #8397df;
        color: #fbfdff;
     }
    
     // post container
     .post-container {
        .post-item {
            .tag-item {
                background: #d7ddef;
                color: #4a5067;     
                
                &:before {
                    border-right-color:#d7ddef;
                }
            }
        }  
    }    
}

