@charset "UTF-8";

.post-container {
    a {
        &:hover {
            text-decoration: none;
        }
    }

    .post-item {
        margin-bottom: 3.5rem;

        .title {
            white-space: nowrap;
            text-overflow: ellipsis;
            overflow: hidden;
            padding: 10px 0;
            font-weight: bold;
        
            &:hover {
                opacity: .8;
            }
        }
    
        .content-summary {
            opacity: .8;
            font-size: 15px;
            overflow: hidden; 
            text-overflow: ellipsis; 
            display: -webkit-box; 
            -webkit-line-clamp: 3; 
            -webkit-box-orient: vertical; 
            word-wrap:break-word;

            &:hover {
                opacity: .6;
            }
        } 

        .thumbnail {
            width: 100%;
            height: 180px;
            object-fit: cover;
            border: 1px solid #f5f5f5;

            &:hover {
                opacity: .8;
            }
        }

        .tag-container {
            list-style: none;
        }

        .tag-item {
            position: relative;
            display: inline-block;
            margin-right: 6px;
            padding: 1px 10px;
            font-size: 13px;
            border-top-left-radius:0;
            border-bottom-left-radius:0;
            margin-left:10px;
            font-weight: 600;

            &:before {
                content: '';
                right: 100%;
                top: 50%;
                border: solid transparent;
                height: 0;
                width: 0;
                position: absolute;
                border-width: 10px;
                border-bottom-width: 9px;
                margin-top: -10px;
            }
        }
    }
}

.menu-badge {
    // ToDO : partial parameter  
    display: none;

    font-size: 12px;
    font-weight: bold;
    padding: 2px 9px;
    border-radius: 5px;
    border: 1px solid transparent;

    &:before {
        margin-right: .3rem;
        font: normal normal normal 14px/1 FontAwesome;
        font-size: 11px;
    }

    &.tech:before {
        content: '\f109';
    }

    &.life:before {
        content: '\f030';
    }
}

