@charset "UTF-8";

.black-theme {
    color: $black-theme-default-text-color;
    background: #feffff;

    a {
        color: $black-theme-default-text-color;
    }

    .site-visual {
        background: linear-gradient(to bottom,#232526 0%,#37393a 45%, #ffffffb3 45%, #ffffffb3 100%);
    }

    .menu-badge {
        border: 1px solid #b0bbe2;
        background: #8397df;
        color: #fbfdff;
     }
    
     // post container
     .post-container {
        .post-item {
            .tag-item {
                background: #d5dcde;
                color: #5c6163;     
                
                &:before {
                    border-right-color:#d5dcde;
                }
            }
        }  
    }    
}

